import { GatsbyBrowser } from "gatsby";
import React from "react";
import { Helmet } from "react-helmet";
import "./src/styles/index.css";
import PreviewImage from "./src/images/PreviewImage.png";

export const wrapPageElement: GatsbyBrowser["wrapPageElement"] = ({
  element,
  props,
}) => {
  return (
    <>
      <Helmet>
        <meta
          name="viewport"
          content="width=device-width, initial-scale=1, maximum-scale=1, user-scalable=0"
        ></meta>
        <meta
          name="description"
          content="Messaging APIs built different."
        ></meta>
        <meta property="og:title" content="Telgorithm" />
        <meta property="og:type" content="article" />
        <meta property="og:image" content={PreviewImage} />
        <meta
          property="og:description"
          content="Messaging APIs built different."
        />
        <meta property="og:site_name" content="Telgorithm" />
        <meta property="twitter:title" content="Telgorithm" />
        <meta
          property="twitter:description"
          content="Messaging APIs built different."
        />
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:image" content={PreviewImage}></meta>
        <meta name="twitter:image:alt" content="Telgorithm" />
        <script
          type="text/javascript"
          id="hs-script-loader"
          async
          defer
          src="//js.hs-scripts.com/21630623.js"
        ></script>
        <script
          src="https://ws.zoominfo.com/pixel/627aea554f6f68001256c330"
          async
          referrerPolicy="unsafe-url"
        ></script>
        <noscript>
          {`<img
              src="https://ws.zoominfo.com/pixel/627aea554f6f68001256c330"
              width="1"
              height="1"
              style={{ display: "none" }}
              alt="websights"
            ></img>`}
        </noscript>
        <script
          async
          src="https://www.googletagmanager.com/gtag/js?id=G-6S9SR1H9J6"
        ></script>
        <script>
          {`window.dataLayer = window.dataLayer || [];
  function gtag(){dataLayer.push(arguments);}
  gtag('js', new Date());  gtag('config', 'G-6S9SR1H9J6');`}
        </script>
      </Helmet>
      {element}
    </>
  );
};
